<div class="stats-grid p-6"><div class="level"><div class="level-left"> <div class="level-item"><h1 class="accent-line">Overview</h1></div></div><div class="level-right"><div class="level-item"><div class="dropdown is-right" :class="{ 'is-active': dropdownIsActive }"><div class="dropdown-trigger"><button class="button" @click="toggleDropdown" aria-haspopup="true" aria-controls="dropdown-menu"><span>Date</span><font-awesome-icon icon="angle-down"></font-awesome-icon></button></div><div class="dropdown-menu" id="dropdown-menu" role="menu"><div class="dropdown-content"><div class="calendar"><span>TO DO: Calendar</span></div><a class="dropdown-item">Ok</a></div></div></div></div></div></div><div class="columns pb-3"><div class="column"><StatComponent v-if="stats[0] &amp;&amp; stats[0].icon" v-bind="stats[0]" :iconColor="stats[0].color"></StatComponent></div><div class="column"><StatComponent v-if="stats[1] &amp;&amp; stats[1].icon" v-bind="stats[1]" :iconColor="stats[1].color"></StatComponent></div><div class="column"><StatComponent v-if="stats[2] &amp;&amp; stats[2].icon" v-bind="stats[2]" :iconColor="stats[2].color"></StatComponent></div><div class="column"><StatComponent v-if="stats[3] &amp;&amp; stats[3].icon" v-bind="stats[3]" :iconColor="stats[3].color"></StatComponent></div></div></div>